import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useSelector, useDispatch } from 'react-redux';
import Button from '@material-ui/core/Button';
import InputGroup from 'react-bootstrap/InputGroup';
import FormControl from 'react-bootstrap/FormControl';
import GestureIcon from '@material-ui/icons/Gesture';
import playedSecFunc from '../../functions/playedSecFunc';
import { urlToImage, MODE_SKETCH } from '../../reducers/viewReducer';
import { changeFormData } from '../../reducers/infostampReducer';
import { PAUSE_PLAYER, PLAY_PLAYER } from '../../reducers/playerReducer';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    height: '80px',
    //backgroundColor: 'grey',
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  formContainer: {
    width: '100%',
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center',
  },
  inputContainer: {
    width: '90%',
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
  },
  inputForm: {
    width: '100%',
  },
  br1: {
    height: 10,
    width: 10,
  },
  br2: {
    height: 5,
    width: 5,
  },
  buttonContainer: {
    width: '100%',
    height: '100%',
  },
}))

const InfostampForm = ({ playedSec, name, canvasRef }) => {
  const dispatch = useDispatch();
  const classes = useStyles();
  const { playing } = useSelector(state => state.player);
  const [timeChange, setTimeChange] = useState(true);
  const [disableSubmit, setDisableSubmit] = useState(true);
  const [infostamp, setInfostamp] = useState({
    time: '',
    url: '',
    info: '',
  });

  const handleTimeStop = () => {
    setTimeChange(false);
    setInfostamp({
      ...infostamp,
      time: playedSec, 
    });
    dispatch({type: PAUSE_PLAYER});
  }

  const handleTimeStart = () => {
    setTimeChange(true);
    dispatch({type: PLAY_PLAYER});
  }

  useEffect(() => {
    const regex_url = /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,4}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/g;
    if(!timeChange && infostamp.url.match(regex_url)){
      setDisableSubmit(false);
    } else{
      setDisableSubmit(true);
    }
  }, [timeChange, infostamp.time, infostamp.url, infostamp.info])

  useEffect(() => {
    if(playing) {
      handleTimeStart();
    } else {
      handleTimeStop();
    }
  }, [playing])

  const handleChangeForm = (e) => {
    
    setInfostamp({
      ...infostamp,
      [e.target.id]: e.target.value,
    });
  }

  const handleSubmitInfostamp = (e) => {
    e.preventDefault();
    canvasRef.current && canvasRef.current.clear();
    dispatch(changeFormData(infostamp));
    dispatch(urlToImage(infostamp.url));
    dispatch({type: MODE_SKETCH});
  } 

  return (
    <div className={classes.root}>
      {
        timeChange ? 
        <Button variant="contained" color="primary" onClick={handleTimeStop}>
          {playedSecFunc.SecTohhmmss(playedSec)}<br/>선택
        </Button>
        :
        <Button variant="contained" color="secondary" onClick={handleTimeStart}>
          {playedSecFunc.SecTohhmmss(infostamp.time)}<br/>취소
        </Button>
      }
      <div className={classes.br1}></div>
      <form onSubmit={handleSubmitInfostamp} className={classes.formContainer}>
        <div className={classes.inputContainer}>
          <div className={classes.inputForm}>
            <InputGroup size="sm">
              <InputGroup.Prepend>
                <InputGroup.Text >URL</InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl id="url" onChange={handleChangeForm} aria-label="url" aria-describedby="inputURL" 
                placeholder="http로 시작하는 웹 사이트 주소 ( 주소 창 복붙하면 됩니다. )"
              />
            </InputGroup>
            <div className={classes.br2}></div>
            <InputGroup size="sm">
              <InputGroup.Prepend>
                <InputGroup.Text>설명</InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl id="info" onChange={handleChangeForm} aria-label="info" aria-describedby="inputInfo"
                placeholder="간단한 설명" 
              />
            </InputGroup>
          </div>
        </div>
        <div className={classes.br1}></div>
        <Button
          color="primary"
          type="submit"
          variant="contained"
          disabled={disableSubmit}
        >
          <GestureIcon/>
        </Button>
      </form>
    </div>
  )
}

export default InfostampForm;
